<template>
  <div id="main">
    <div id="click-section">
      <div id="drawerboxes">
        <!-- 抽屉按钮组 -->
        <div
          v-for="(item, index) in drawerItems"
          :key="index"
          class="drawerbox"
          :class="{ active: item.active }"
        >
          <button
            :class="{ 'drawer-btn': true, active: item.active }"
            @click="slideTo(index + 1)"
          >
            {{ item.title }}<span class="drawer-head">{{ index + 1 }}</span>
          </button>
        </div>
      </div>
    </div>
    <div id="slide-section">
      <div id="slide-bar">
        <div id="bar" :style="barStyle"></div>
      </div>
      <div id="card-section">
        <!-- 卡片组 -->
        <div
          v-for="(card, index) in cards"
          :key="index"
          :class="{ card: true, active: index === chosenSlideNumber - 1 }"
          :style="cardStyle"
        >
          <component :is="card.component" />

        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import { markRaw } from 'vue';
import Info_Data from "@/components/Info_Data.vue";
import SkillIntroduction from "@/components/SkillIntroduction.vue";
import ExperienceComponent from "@/components/ExperienceComponent.vue";
import HobbyPanels from "@/components/HobbyPanels.vue";

// 定义变量
const chosenSlideNumber = ref(1); // 当前选择的幻灯片编号
const offset = ref(0); // 幻灯片偏移量
const barOffset = ref(0); // 导航条偏移量
let intervalID; // 定时器 ID

// 抽屉项数据
const drawerItems = ref([
  { title: "基本信息", active: true },
  { title: "专业技能", active: false },
  { title: "过往经历", active: false },
  { title: "兴趣爱好", active: false },
]);


// 卡片数据
const cards = ref([
  {
    component: markRaw(Info_Data),
    className: "active",
  },
  {
    component: markRaw(SkillIntroduction),
    className: "",
  },
    {
    component: markRaw(ExperienceComponent),
    className: "",
  },
      {
    component: markRaw(HobbyPanels),
    className: "",
  },
]);

// 启动幻灯片轮播
function startSlide() {
  intervalID = setInterval(() => {
    slideTo((chosenSlideNumber.value % cards.value.length) + 1); // 每次切换到下一个幻灯片
  }, 5000); // 每隔 3 秒自动切换幻灯片
}

// 鼠标移入幻灯片区域时清除定时器
function handleMouseEnter() {
  clearInterval(intervalID);
}

// 鼠标移出幻灯片区域时重新启动幻灯片轮播
function handleMouseLeave() {
  startSlide();
}

// 切换到指定编号的幻灯片
function slideTo(slideNumber) {
  slideNumber = Math.max(1, Math.min(slideNumber, cards.value.length));
  drawerboxToggle(slideNumber); // 切换抽屉面板状态
  chosenSlideNumber.value = slideNumber;
  offset.value = (chosenSlideNumber.value - 1) * -100; // 计算幻灯片偏移量
  barOffset.value = (chosenSlideNumber.value - 1) * 100; // 计算导航条偏移量
  barSlide(barOffset.value); // 移动导航条
}

// 切换抽屉面板状态
function drawerboxToggle(drawerboxNumber) {
  drawerItems.value.forEach((item, index) => {
    item.active = index + 1 === drawerboxNumber;
  });
}

// 移动导航条
function barSlide(newBarOffset) {
  barOffset.value = newBarOffset;
}

// 计算导航条样式
const barStyle = computed(() => ({
  transform: `translateY(${barOffset.value}%)`,
}));

// 计算卡片样式
const cardStyle = computed(() => ({
  transform: `translateY(${offset.value}%)`,
}));

onMounted(() => {
  startSlide();
  const slideSection = document.querySelector("#slide-section");
  slideSection.addEventListener("mouseenter", handleMouseEnter);
  slideSection.addEventListener("mouseleave", handleMouseLeave);
});
</script>

<style scoped>
/* 基本重置 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* 背景图片 */
/* body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('@/assets/img/bg.jpg');
  background-size: cover;
} */

/* 主要容器 */
#main {
  height: 80vh;
  width: 80vw;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background: #f5f4f1; */
}

/* 点击区域 */
#click-section {
  width: 35%;
  height: 100%;
  padding: 20px 0;
  position: relative;
  border-radius: 10px;
  /* background: #f5f4f1; */
}

/* 抽屉盒子 */
#drawerboxes {
  margin-left: 10%;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

/* 抽屉盒子前的竖线 */
#drawerboxes::before {
  content: "";
  width: 8px;
  height: 94%;
  position: absolute;
  left: -10px;
  top: 3%;
  z-index: 120;
  background-color: #ccc; /* 示例颜色 */
}

/* 单个抽屉盒子 */
.drawerbox {
  height: calc(100% / 6.5);
  width: 70%;
  position: relative;
  z-index: 100;
  transform: translateX(-70%);
  transition: transform 0.5s ease-in-out;
}

/* 激活状态的抽屉盒子 */
.drawerbox.active {
  transform: translateX(0);
}

/* 抽屉按钮 */
.drawer-btn {
  width: 100%;
  height: 100%;
  font: 800 30px "";
  background-color: rgb(175, 190, 255);
  border: none;
  transition: background-color 0.5s ease-in-out;
  color: #ffffff00;
}

/* 激活状态的抽屉按钮 */
.drawer-btn.active {
  background-image: url("@/assets/img/bg.jpg");
  background-size: cover;
  color: rgb(70, 100, 180);
}

/* 抽屉按钮悬停效果 */
.drawer-btn:hover {
  cursor: pointer;
}

/* 抽屉头部 */
.drawer-head {
  position: absolute;
  color: rgb(255, 255, 255);
  font-size: 160px;
  font-weight: 650;
  right: -38px;
  top: calc(50% - 110px);
  text-shadow: 2px -1px 8px rgba(140, 183, 244, 0.323);
}

/* 滑动区域 */
#slide-section {
  position: relative;
  height: 100%;
  width: 65%;
  display: flex;
  justify-content: center;
  padding: 0 40px;
  /* background-color: #cccbc8; */
  margin-left: 20px;
  border-radius: 10px;



background-color: rgba(255, 255, 255, 0.2);
backdrop-filter: blur(7.5px);
-webkit-backdrop-filter: blur(7.5px);
border: 0.8px solid rgba(255, 255, 255, 0.18);
box-shadow: rgba(142, 142, 142, 0.19) 0px 6px 15px 0px;
-webkit-box-shadow: rgba(142, 142, 142, 0.19) 0px 6px 15px 0px;
border-radius: 10px;
-webkit-border-radius: 10px;
color: rgba(255, 255, 255, 0.6);
                
  /* background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.6),
    rgba(255, 255, 255, 0.3),
    rgba(255, 255, 255, 0.2)
  ); */
  /* backdrop-filter: blur(11px); */

}


/* 卡片区域 */
#card-section {
  height: 100%;
  width: 80%;
  overflow: hidden;
}

/* 卡片 */
/* 卡片 */
.card {
  display: flex;
    position: relative; /* 设置相对定位，以便使用绝对定位的伪元素 */
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding: 10% 0;
  color: white;
  font-size: 30px;
  transition: transform 0.5s ease-in-out;
  opacity: 0;
  transform: translateY(100%);
  overflow-y: auto; /* 允许垂直滚动 */
  overflow-x: hidden; /* 隐藏水平滚动条 */
  /* 其他样式 */
}


/* 定义滚动条轨道样式 */
.card::-webkit-scrollbar {
  width: 5px; /* 设置滚动条轨道的宽度 */
  background-color: transparent; /* 轨道背景透明 */
}

/* 定义滚动条滑块样式 */
.card::-webkit-scrollbar-thumb {
  background-color: transparent; /* 设置滑块颜色为透明 */
  border-radius: 2.5px; /* 设置滑块圆角 */
  border: none; /* 移除滑块边框 */
}

/* 定义滚动条轨道的圆角 */
.card::-webkit-scrollbar-track {
  border-radius: 2.5px; /* 轨道圆角 */
  background-color: transparent; /* 轨道背景透明 */
}

/* Firefox滚动条美化 */
.card {
  scrollbar-color: transparent transparent; /* Firefox的滚动条颜色设置为透明 */
  scrollbar-width: thin; /* 设置滚动条宽度 */
}



#slide-bar {
  position: absolute;
  top: 10%;
  left: 40px;
  height: 80%;
  width: 1px;
  background-color: rgb(223, 223, 223);
}

#bar {
  position: absolute;
  height: calc(100% / 4);
  width: 5px;
  top: 0;
  left: -1.2px;
  background-color: #71c4ef;
  transition: transform 0.5s ease-in-out;
}

#card-section {
  height: 100%;
  width: 80%;
  overflow: hidden;
}

/* 激活状态的卡片 */
.card.active {
  opacity: 1;
  transform: translateY(0);
}
</style>