<template>
  <div class="background">
    <div class="bg-squares">
      <li v-for="n in 10" :key="n" class="bg-square" :style="getBgSquareStyle(n)"></li>
    </div>
  </div>
</template>

<script setup>
// 由于我们不需要响应式变量，所以这里不使用ref

const getBgSquareStyle = (n) => {
  const positions = [10, 20, 25, 40, 70, 80, 32, 55, 25, 90];
  const sizes = [[40, 40], [80, 80], [40, 40], [60, 60], [40, 40], [120, 120], [160, 160], [20, 20], [10, 10], [160, 160]];
  const delays = [0, 2, 4, 0, 0, 3, 7, 15, 2, 11];
  const durations = [20, 17, 20, 22, 20, 20, 20, 40, 40, 20];
  const colors = [0.15, 0.15, 0.15, 0.25, 0.15, 0.2, 0.15, 0.15, 0.3, 0.15];

  return {
    left: `${positions[n - 1]}%`,
    width: `${sizes[n - 1][0]}px`,
    height: `${sizes[n - 1][1]}px`,
    backgroundColor: `rgba(255, 255, 255, ${colors[n - 1]})`,
    animationDelay: `${delays[n - 1]}s`,
    animationDuration: `${durations[n - 1]}s`
  };
};
</script>

<style scoped>
.background {
  height: 100vh;
background: linear-gradient(to right, #B3E5FC, #FFB6C1 35%, #FFC0CB 60%, #FFD194 80%, #FFD700 85%, #FFD700 100%);
  position: fixed; /* 或者 absolute，取决于你的需求 */
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1; /* 设置一个较低的 z-index 值 */
}

.bg-squares {
  list-style: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.bg-squares .bg-square {
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.15);
  position: absolute;
  bottom: -160px;
  animation: square 20s linear infinite;
}

/* 为每一个方块设置不同的位置、大小、动画延迟时间、动画时长、背景色 */
.bg-square-1 { left: 10%; }
.bg-square-2 { left: 20%; width: 80px; height: 80px; animation-delay: 2s; animation-duration: 17s; }
.bg-square-3 { left: 25%; animation-delay: 4s; }
.bg-square-4 { left: 40%; width: 60px; height: 60px; background-color: rgba(255, 255, 255, 0.25); animation-duration: 22s; }
.bg-square-5 { left: 70%; }
.bg-square-6 { left: 80%; width: 120px; height: 120px; background-color: rgba(255, 255, 255, 0.2); animation-delay: 3s; }
.bg-square-7 { left: 32%; width: 160px; height: 160px; animation-delay: 7s; }
.bg-square-8 { left: 55%; width: 20px; height: 20px; animation-delay: 15s; animation-duration: 40s; }
.bg-square-9 { left: 25%; width: 10px; height: 10px; background-color: rgba(255, 255, 255, 0.3); animation-delay: 2s; animation-duration: 40s; }
.bg-square-10 { left: 90%; width: 160px; height: 160px; animation-delay: 11s; }

@keyframes square {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-120vh) rotate(600deg);
  }
}
</style>