<template>
<div class="man">
  <div class="shell">
    <div
      v-for="(image, index) in images"
      :key="index"
      class="box"
      @mouseover="handleMouseOver(index)"
      @mouseleave="handleMouseLeave"
    >
      <div class="images">
        <img :src="require(`@/assets/img/${image}`)" />
      </div>
      <div class="content">
        <h2>记忆</h2>
        <p>2019-05-21</p>
      </div>
    </div>
  </div>
</div>
</template>

<script setup>
import { ref } from 'vue';

const images = ref([
  "en1.jpg",
  "en2.jpg",
  "en3.jpg",
  "en4.jpg",
  "banner.jpg"
]);

const hoveredIndex = ref(-1);

function handleMouseOver(index) {
  hoveredIndex.value = index;
}

function handleMouseLeave() {
  hoveredIndex.value = -1;
}
</script>

<style scoped>
.man {
  display: flex;
  justify-content: center;
  align-items: center;
  /* min-height: 100vh; */
  /* background-color: #cccbc8; */
  height: 80vh;

background-color: rgba(255, 255, 255, 0.2);
backdrop-filter: blur(7.5px);
-webkit-backdrop-filter: blur(7.5px);
border: 0.8px solid rgba(255, 255, 255, 0.18);
box-shadow: rgba(142, 142, 142, 0.19) 0px 6px 15px 0px;
-webkit-box-shadow: rgba(142, 142, 142, 0.19) 0px 6px 15px 0px;
border-radius: 10px;
-webkit-border-radius: 10px;
color: rgba(255, 255, 255, 0.6);
                
}

.shell {
  position: relative;
  min-width: 1000px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  transform-style: preserve-3d;
  perspective: 900px;
}

.box {
  position: relative;
  width: 220px; /* 放大1.2倍后的宽度 */
  height: 440px; /* 放大1.2倍后的高度 */
  transition: 0.3s;
  overflow: hidden;
  margin: 30px;
  border-radius: 5px;
  border: #fff 5px solid;
}

.shell:hover .box:not(:hover) {
  transform: rotateY(20deg);
}

.box:hover {
  transform: rotateY(0deg) scale(1.25);
  box-shadow: 0 25px 40px rgba(0, 0, 0, 0.7);
  z-index: 1;
}

.box:hover ~ .box:not(:hover) {
  transform: rotateY(-20deg);
}

.images {
  width: 100%;
  height: 100%;
  overflow: hidden; /* 确保图片超出部分被隐藏 */
  position: absolute; /* 使图片相对于容器定位 */
  top: 0;
  left: 0;
}

.images img {
  width: 100%; /* 设置图片宽度为容器的100% */
  height: 100%; /* 设置图片高度为容器的100% */
  object-fit: contain; /* 保持图片的宽高比，确保图片完全包含在容器内 */
  position: absolute; /* 使图片可以相对于容器定位 */
  top: 0; /* 定位到容器的顶部 */
  left: 0; /* 定位到容器的左侧 */
}

.content {
  position: relative;
  top: 0;
  width: 100%;
  height: 20%;
  z-index: 999;
  padding: 15px;
  /* background-color: rgba(255, 255, 255, 0.7); */
}

h2 {
  color: rgb(210, 140, 140);
  transition: 0.3s;
  font-size: 20px;
  transform: translateY(-100px);
}

.box:hover h2 {
  transform: translateY(-15px);
}

p {
  color: rgb(0, 0, 0);
  transition: 0.3s;
  font-size: 14px;
  transform: translateY(600px);
}

.box:hover p {
  transform: translateY(220px);
}
</style>