<template>
  <div class="split-layout" :class="{ stacked: isStacked }">
    <div class="side left" :style="leftStyle">
      <slot name="left"></slot>
    </div>
    <div class="side right" :style="rightStyle">
      <slot name="right"></slot>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onBeforeUnmount, defineProps } from "vue";

// Props to receive the width ratios
const props = defineProps({
  leftRatio: {
    type: Number,
    default: 50, // default to 50% if not provided
  },
  rightRatio: {
    type: Number,
    default: 50, // default to 50% if not provided
  },
});

const isStacked = ref(false);

// Calculate the actual width based on the ratio
const leftStyle = computed(() => {
  return {
    flexBasis: `${props.leftRatio}%`,
    maxWidth: `${props.leftRatio}%`,
  };
});

const rightStyle = computed(() => {
  return {
    flexBasis: `${props.rightRatio}%`,
    maxWidth: `${props.rightRatio}%`,
  };
});

// Listen for window resize and adjust layout based on width
const handleResize = () => {
  isStacked.value = window.innerWidth < 768;
};

onMounted(() => {
  handleResize(); // Initialize the layout
  window.addEventListener("resize", handleResize);
});

onBeforeUnmount(() => {
  window.removeEventListener("resize", handleResize);
});
</script>

<style scoped>
.split-layout {
  display: flex;
  flex-direction: row;
  width: 90%;
}

.split-layout.stacked {
  flex-direction: column;
}

.split-layout .side {
  overflow-y: auto;
  transition: flex-basis 0.3s ease;
}

.split-layout .left {
  /* background-color: #f5f4f1; */
  display: flex;
  border-radius: 10px;
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
  overflow: hidden; /* 隐藏滚动条 */


background-color: rgba(255, 255, 255, 0.25);
backdrop-filter: blur(6px);
-webkit-backdrop-filter: blur(6px);
border: 0.8px solid rgba(255, 255, 255, 0.18);
box-shadow: rgba(142, 142, 142, 0.19) 0px 6px 15px 0px;
-webkit-box-shadow: rgba(142, 142, 142, 0.19) 0px 6px 15px 0px;
color: rgba(255, 255, 255, 0.75);
                
}

.split-layout .right {
  /* background-color: #e0e0e0; Customize as needed */
  display: flex;
  margin-left: 20px;;
  border-radius: 10px;
  justify-content: center; /* 水平居中 */
  /* padding: ; */
  align-items: center; /* 垂直居中 */
  overflow: hidden; /* 隐藏滚动条 */
  background-color: #d4eaf7;
}
</style>