<template>
  <div class="accordion-shell">
    <div class="box" v-for="(item, index) in accordionItems" :key="index"
         @mouseenter="showTitle(index)"
         @mouseleave="hideTitle(index)">
      <img :src="require(`@/assets/img/${item.imgSrc}`)" :alt="item.title">
      <span v-if="item.showTitle" class="title-overlay">{{ item.title }}</span>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';

const accordionItems = ref([
  { imgSrc: "lkcode.png", title: "编程", showTitle: false },
  { imgSrc: "lkread.jpg", title: "阅读", showTitle: false },
  { imgSrc: "lkbadminton.jpg", title: "羽毛球", showTitle: false },
  { imgSrc: "lkgt.png", title: "吉他", showTitle: false },
]);

const showTitle = (index) => {
  accordionItems.value[index].showTitle = true;
};

const hideTitle = (index) => {
  accordionItems.value[index].showTitle = false;
};
</script>

<style scoped>
.accordion-shell {
  display: flex;
  justify-content: center;
  width: 90%;
  height: 700px;
  overflow-x: auto;
  padding: 20px; /* 为滚动条留出空间 */
}

.box {
  flex: 1;
  overflow: hidden;
  transition: .5s;
  margin: 0 20px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, .5);
  border-radius: 20px;
  border: 10px solid #fff;
  background-color: #fff;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%; /* 确保盒子填满高度 */
}

.box img {
  min-width: 100%; /* 确保图片宽度至少为100% */
  min-height: 100%; /* 确保图片高度至少为100% */
  object-fit: cover; /* 覆盖并保持图片宽高比 */
  transition: .5s;
}

.box span.title-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  padding: 10px;
  font: 200 20px '优设标题黑';
  text-align: center;
  opacity: 0;
  transition: opacity 0.5s;
  pointer-events: none; /* 防止文字被点击 */
}

.box:hover span.title-overlay {
  opacity: 1;
}

.box span {
  font: 200 45px '优设标题黑';
  text-align: center;
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box:hover {
  flex-basis: 40%;
}

.box:hover img {
  width: 100%;
  height: 100%;
}
</style>