<template>
  <div class="email-container">
    <div class="email-form">
      <h1>发送邮件</h1>
      <input type="email" v-model="emailSubject" placeholder="邮件主题" />
      <textarea v-model="emailContent" placeholder="邮件内容"></textarea>
      <button @click="sendEmail" id="submitBtn">发送</button>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";

export default {
  name: "MessageBoard",
  setup() {
    const emailSubject = ref("");
    const emailContent = ref("");
    const recipientEmail = "qwanjun@outlook.com"; // 默认收件人邮箱地址

    function sendEmail() {
      if (emailContent.value === "") {
        alert("请输入邮件内容");
        return;
      }
      // 构造邮箱链接
      const emailLink = `mailto:${recipientEmail}?subject=${encodeURIComponent(
        emailSubject.value
      )}&body=${encodeURIComponent(emailContent.value)}`;
      // 打开邮箱客户端或网页
      window.location.href = emailLink;
    }

    return {
      emailSubject,
      emailContent,
      sendEmail,
    };
  },
};
</script>

<style scoped>
.email-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 80vh;
  border-radius: 10px;
  /* background-image: linear-gradient(90deg, #e0c3fc 0%, #8ec5fc 100%); */
}

.email-form {
  background-color: #f5f4f1;
/* box-shadow:  -5px 5px 10px #c9c9c8,
             5px -5px 10px #bcbcbb; */
  padding: 2rem;
  border-radius: 10px;
  
  width: 90%;
  height: auto;
  max-width: 600px;
  margin: auto;
}

.email-form h1 {
  text-align: center;
  color: #333;
  font-size: 24px;
  margin-bottom: 1.5rem;
}

input[type="email"],
textarea {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

textarea {
  outline: none;
  border-color: #007bff;
  height: 128px;
}


input[type="email"]:focus,
textarea:focus {
  outline: none;
  border-color: #007bff;
}

#submitBtn {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 0.75rem 1.5rem;
  width: 200px;
  height: 50px;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

#submitBtn:hover {
  background-color: #0056b3;
}
</style>