<template>
  <nav class="navbar">
    <ul class="nav-list">
      <li v-for="item in navItems" :key="item" class="nav-item">
        <!-- 使用 `router-link-active` 类来高亮当前激活的链接 -->
        <router-link
          :to="item.link"
          :class="{ 'router-link-active': route.name === item.link }"
        >
          {{ item.name }}
        </router-link>
      </li>
    </ul>
  </nav>
</template>

<script setup>
import { ref } from 'vue';
import { useRoute } from 'vue-router';

const navItems = ref([
  {name:'主页',link:'ProfileCard'},
  {name:'关于我',link:'About'},
  {name:'作品',link:'Services'},
  {name:'与我联系',link:'ContactForm'},
]
);
const route = useRoute();
</script>

<style scoped>
.navbar {
  width: 500px; 
  display: flex;
  background: #FEFEFE;
  border-radius: 50px; /* 设置圆角 */
}

.nav-list {
  list-style: none;
  display: flex;
  background-color: #FEFEFE;
  border-radius: 20px; /* 设置圆角 */
  padding: 0; /* 移除默认的padding */
  margin: 0; /* 移除默认的margin */
}

.nav-item {
  margin: 0 5px; /* 为导航项添加左右边距 */
}


.nav-item a {
  text-decoration: none;
  color: rgb(70, 100, 180);
  border-radius: 20px; /* 设置圆角 */
  padding: 10px 20px; /* 增加padding */
  margin: 0; /* 确保没有额外的margin */
  display: block; /* 使链接填满整个li */
  box-sizing: border-box; /* 确保padding和border包含在宽度内 */
  transition: background-color 0.5s; /* 平滑过渡效果 */
}

.nav-item a:hover,
.nav-item a.router-link-active {
  background-color: #71c4ef;
  color: #672424;
  transform: translateX(5px); /* 向右侧平移5px */
  transition: background-color 0.3s, color 0.3s, transform 0.3s; /* 平滑过渡效果 */
}
</style>